/* Ensure the body takes up the full viewport and centers content */
body {
  margin: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: #eaeaea; /* Light background color for contrast */
}

.chatbox {
  width: 600px;          /* Increase width */
  height: 700px;         /* Increase height */
  display: flex;
  flex-direction: column;
  border: 1px solid #ddd;
  border-radius: 8px;
  overflow: hidden;
  background-color: #fff;
}

.chatbox-header {
  background-color: #007bff;
  color: #fff;
  padding: 15px;
  font-weight: bold;
  text-align: center;
  font-size: 1.2em; /* Increase font size */
}

.chatbox-body {
  flex: 1;
  overflow-y: auto;
  padding: 15px;
  background-color: #f1f1f1;
}

.messages {
  display: flex;
  flex-direction: column;
}

.message {
  padding: 10px;
  border-radius: 5px;
  margin-bottom: 10px;
  background-color: #fff;
  border: 1px solid #ddd;
  word-wrap: break-word; /* Ensures long words break and wrap */
  white-space: pre-wrap; /* Preserves whitespace and allows wrapping */
}

.chatbox-footer {
  display: flex;
  flex-direction: column;
  padding: 15px;
  background-color: #fff;
  border-top: 1px solid #ddd;
}

.chatbox-footer textarea {
  width: 100%;
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 4px;
  resize: none; /* Prevent manual resizing */
  overflow: auto; /* Allow scroll when content overflows */
  line-height: 1.5em; /* Line height for calculation */
  max-height: 120px; /* Max height (5 lines * line-height) */
  box-sizing: border-box; /* Include padding and border in the height */
  font-size: 1em; /* Consistent font size */
}

.chatbox-footer button {
  margin-top: 10px;
  padding: 10px 15px;
  border: none;
  background-color: #007bff;
  color: #fff;
  border-radius: 4px;
  cursor: pointer;
}

.chatbox-footer button:hover {
  background-color: #0056b3;
}
